.site-header {
    background: lightgrey;
    text-align: center;

    @at-root .site-title {
        
        font-family: 'Roboto', sans-serif;
        a { 
            color: black;
            border-bottom: 4px solid black;
            text-shadow: 5px 5px 3px 0px rgba(255,255,255,0.75);
         }
    }

    & p { font-style: italic; }
}