@keyframes navitemin {
    from {
    }

    to {
        opacity: 1;
        transform: scale(1) translate(0,0);
    }
}

.navigation.main {
    //background: linear-gradient(to right, rgba(0, 47, 150, 0.4), rgba(66, 51, 68, 0.4), rgba(0, 47, 150, 0.4));
    background: darkgrey;
    display: flex;
    justify-content: space-between;
    align-items: center;

    & img {
        margin: 0 2rem;
        height: 4rem;
    }

    & > ul {
        margin-right: 5rem;
        display: inline-block;
        transition: opacity 0.2s ease-out, transform 0.2s ease-in;
        transform: translateX(2rem);

        li {
            display: inline-block;
            padding: 1.5rem 0.5rem 0 0.5rem;
            opacity: 0;
            animation: navitemin 0.5s 1 ease forwards;
            transform: scale(0.1) translateX(0, 3rem);
        }

        a {
            font-weight: bold;
            color: #D94B6B;
            display: block;
            padding: 0.5rem 1rem;
            text-decoration: none;
            border-radius: 0.4rem;
            border: solid transparent;
            border-width: 3px 0;
            text-shadow: 2px 2px 4px rgba(255,255,255, 0.8);
        }

        a:hover {
            color: #9766C2;
            padding-top: 0.3rem;
            font-weight: bold;
            border-color: #9766C2;
            text-shadow: 2px 2px 4px rgba(255,255,255, 1);
        }
    }

    @for $i from 1 through 7 {
        & li:nth-child(#{$i}) { animation-delay: $i * 0.15s; }
    }

